.header {
  text-align: center;
}

.navbar {
  background-color: #88c0b9;
  padding: 15px 0;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.navbar li {
  margin: 0 15px;
}

.navbar a {
  color: rgb(19, 19, 19);
  text-decoration: none;
  font-weight: bold;
}


.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 125px 30px;
  background-color: #88c0b9;
  margin-top: 20px;
}

.banner-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  width: 100%;
}

.text-content {
  margin-bottom: 20px;
}

.text-content h1 {
  font-size: 2.5rem;
  margin: 0;
  color: #333;
}

.text-content p {
  font-size: 1.2rem;
  color: #666;
}

.profile-photo img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

@media (min-width: 768px) {
  .banner-content {
      flex-direction: row;
  }

  .text-content {
      margin-right: 50px;
      text-align: left;
  }

  .text-content h1 {
      font-size: 3rem;
  }

  .text-content p {
      font-size: 1.5rem;
  }

  .profile-photo img {
      width: 200px;
      height: 200px;
  }
}

@media (max-width: 480px) {
  .text-content h1 {
      font-size: 1.8rem;
  }

  .text-content p {
      font-size: 1rem;
  }
}
